html {
  --hover-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-right: 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 50px;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list__item {
  margin-right: 20px;
}

.list__item:last-child {
  margin-right: 0;
}

.link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

